@import "main";
// End Of Import

// Preloader
.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #5A0569;
  @include Flex;
  z-index: 99999;

  .spinner {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 0;
    border-left: 4px solid $color-white;
    animation: spin 1s linear infinite;
  }
}
// Preloader

// Mob Nav
.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(#5a026b, 0.95);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;

  span {
    display: none;
    color: #fff;
    font-size: 25px;
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
  }

  &.open {
    opacity: 1;
    visibility: visible;

    span {
      display: block;
    }
  }
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  html[dir="ltr"] & {
    right: unset;
    left: 0;
  }
  width: 100%;
  height: 100%;
  background-color: #5A0569;
  z-index: 999;
  // border-radius: 50%;
  transition: 0.5s;
  @include Flex;
  flex-direction: column;
  // transform: scale(0) translate(10px, 10px);
  // opacity: ;
  transform-origin: right;
  @include LTR {
    transform-origin: left;
  }
  transform: rotateY(-90deg);

  &.open {
    // transform-origin: left;
    // transform: scale(1);
    // border-radius: 0;
    // width: 100%;
    // height: 100%;
    transform: rotateY(0deg);

    .options {
      opacity: 1;
    }

    ul {
      opacity: 1;

      li {
        transform: translateX(0) !important;
        opacity: 1;
      }
    }
  }

  &.origin {
    transform-origin: left;

    html[dir="ltr"] & {
      transform-origin: right;
    }
  }

  .options {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: 50px;
    opacity: 0;

    .lang a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #fff;
      width: 32px;
      height: 32px;
      background-color: transparent;
    }

    .close1 {
      display: flex;
      font-size: 18px;
      color: #fff;
      cursor: pointer;

      span {
        color: #fff;
        html[dir="ltr"] & {
          color: #fff;
        }
      }
    }
  }

  & > ul {
    display: flex;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0.5s;

    & > li {
      position: relative;
      padding: 5px 0;
      // margin-bottom: 20px;
      transition: 0.5s;
      transform: translateX(10px);
      html[dir="ltr"] & {
        transform: translateX(-10px);
      }
      opacity: 0;

      &:nth-child(1) {
        transition-delay: 0.15s;
      }
      &:nth-child(2) {
        transition-delay: 0.3s;
      }
      &:nth-child(3) {
        transition-delay: 0.45s;
      }
      &:nth-child(4) {
        transition-delay: 0.6s;
      }
      &:nth-child(5) {
        transition-delay: .75s;
      }
      &:nth-child(6) {
        transition-delay: .9s;
      }
      &:nth-child(7) {
        transition-delay: 1.05s;
      }
      &:nth-child(8) {
        transition-delay: 1.2s;
      }
      &:nth-child(9) {
        transition-delay: 1.35s;
      }

      &.drop {
        > a {
          position: relative;
          > span {
            margin-right: 10px;
            margin-left: -20px;
            html[dir="ltr"] & {
              margin-right: -20px;
              margin-left: 10px;
            }
            font-size: 10px;
            color: #fff;
          }
        }

        &.open {
          span {
            transform: rotate(180deg);
          }
        }
      }

      a {
        width: 100%;
        display: block;
        font-family: "tjMd";
        position: relative;
        font-size: 17px;
        color: #fff;
        text-align: center;
        transition: 0.5s;
      }

      .megaMenu {
        display: none;
        margin-top: 10px;

        li {
          &:not(:last-child) {
            margin-bottom: 6px;
          }

          a {
            font-size: 15px;
            color: #e4e4e4;
          }
        }
      }

      .drop {
        width: 100%;
        padding: 5px 15px;
        display: none;
        overflow: visible;
        height: auto;

        li {
          padding: 4px 0;

          a {
            font-size: 15px;
            color: #898989;
            transition: 0.5s;

            &:hover {
              color: #595959;
            }
          }
        }
      }
    }
  }
}

