// Products

.products {
  background-image: linear-gradient(
    to top,
    rgb(255, 255, 255),
    rgb(250, 250, 250)
  );
  padding: 80px 0;

  @include Xs{
    padding: 60px 0;
  }

  &.noBG{
    background: none;
  }

  h2 {
    margin: 0;
    font-size: 20px;
    font-family: "tjBold";
    color: #333;
    text-align: center;
    margin-bottom: 30px;
  }

  .owl-carousel .owl-item img {
    width: auto;
  }

  .owl-nav {
    top: 50%;
    display: flex !important;
    width: calc(100% + 86px);

    @include Md {
      width: calc(100% + 40px);
    }

    @include Lg {
      width: calc(100% + 86px);
    }

    @include Sm {
      display: none !important;
    }

    span {
      font-size: 18px;
      color: #333;
      transition: 0.5s;

      &:hover {
        color: #ffb611;
      }
    }
  }

  .owl-dots {
    @include Md {
      display: none;
    }

    @include Sm {
      display: flex;
      bottom: -50px;
    }
    span {
      border-radius: 50%;
      background-color: rgba($color-black, 0.4);
    }

    .owl-dot.active {
      span {
        background-color: #5A0569;
      }
    }
  }
}

.product {
  .pic {
    width: 220px;
    height: 218px;
    @include Flex;
    position: relative;
    background-color: #fff;
    margin-bottom: 20px;

    @include Xs{
      width: auto;
    }

    .over {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @include Flex;
      padding: 0 15px;

      .toCart {
        @include Flex;
        flex-grow: 1;
        height: 32px;
        margin-left: 5px;
        border-radius: 100px;
        background-color: #feb51c;
        color: #fff;
        font-size: 14px;
        font-family: "tjMd";
        transition: 0.5s;

        span {
          color: currentColor;
          font-size: 16px;
          margin-left: 10px;
        }

        &:hover {
          background-color: #5a0569;
        }
      }

      .wish {
        @include Flex;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: #c2c2c2;
        color: #fff;
        transition: 0.5s;

        span {
          color: currentColor;
          font-size: 16px;
        }

        &:hover {
          background-color: #dd042b;
        }
      }

      button {
        transition: 0.5s;
        transform: translateY(20px);
        opacity: 0;
        visibility: hidden;
      }
    }
  }
  &:hover {
    .over {
      button {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .title {
    font-size: 15px;
    color: #333;
    text-align: center;
    margin-bottom: 10px;
    height: 42px;
  }

  .rating {
    text-align: center;

    i {
      font-size: 12px;
      color: #cbcbcb;
      &:not(:last-child) {
        color: #feb51c;
        margin-left: 2px;
        @include LTR {
          margin-right: 2px;
          margin-left: 0;
        }
      }
    }
  }

  .priceSec {
    margin-top: 15px;
    @include Flex;
    .desc {
      font-size: 16px;
      font-family: "tjMd";
      color: #afafaf;
      text-decoration: line-through;

      @include LTR {
        direction: ltr;
      }
    }

    .price {
      font-size: 18px;
      font-family: "tjBold";
      color: #5a026b;

      @include LTR {
        direction: ltr;
      }
    }
    > div {
      &:not(:last-child) {
        margin-left: 10px;
      }
    }
  }
}

