@mixin mob320 {
  @media (max-width: 320px) { @content };
}
@mixin mob375 {
  @media (max-width: 375px) { @content };
}
@mixin mob425 {
  @media (max-width: 425px) { @content };
}
@mixin mob445 {
  @media (max-width: 445px) { @content };
}
@mixin mob480 {
  @media (max-width: 480px) { @content };
}
@mixin mob600 {
  @media (max-width: 600px) { @content };
}

@mixin Xs {
  @media (max-width: 767px) {@content};
}
//For Small
@mixin Sm {
  @media (max-width: 991px) {@content};
}
//For Medium
@mixin Md {
  @media (min-width: 992px) {@content;}
}
//For Extra Large
@mixin Lg {
  @media (min-width: 1200px) {@content;}
}


// English
@mixin LTR {
  html[dir="ltr"] & {@content};
}




// Loader
@mixin global-bg() {
  background-color: $primary-color;
}

@mixin global-animation() {
  animation-fill-mode: both;
}

@mixin balls() {
  @include global-bg();

  width: $ball-size;
  height: $ball-size;
  border-radius: 100%;
  margin: $margin;
}

@mixin lines() {
  @include global-bg();

  width: $line-width;
  height: $line-height;
  border-radius: 2px;
  margin: $margin;
}
