body {
  font-family: "tj", sans-serif;
}

* {
  font-family: inherit;
}

input,
a,
button {
  font-family: "tj", sans-serif;

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;
  border: 0;
  background-color: #FFF;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }

  &:hover,
  &:active,
  &:focus {
    background-color: #FFF;
    box-shadow: none;
  }
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}
