@keyframes ball-grid-beat {
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

.ball-grid-beat {
  width: 57px;
}

.ball-grid-beat > div:nth-child(1) {
  animation-delay: -0.04s;
  animation-duration: 1.3s;
}

.ball-grid-beat > div:nth-child(2) {
  animation-delay: -0.1s;
  animation-duration: 1.26s;
}

.ball-grid-beat > div:nth-child(3) {
  animation-delay: 0.28s;
  animation-duration: 1.33s;
}

.ball-grid-beat > div:nth-child(4) {
  animation-delay: 0s;
  animation-duration: 0.77s;
}

.ball-grid-beat > div:nth-child(5) {
  animation-delay: 0.47s;
  animation-duration: 1.13s;
}

.ball-grid-beat > div:nth-child(6) {
  animation-delay: 0.46s;
  animation-duration: 0.73s;
}

.ball-grid-beat > div:nth-child(7) {
  animation-delay: 0.26s;
  animation-duration: 0.79s;
}

.ball-grid-beat > div:nth-child(8) {
  animation-delay: -0.15s;
  animation-duration: 1.06s;
}

.ball-grid-beat > div:nth-child(9) {
  animation-delay: -0.13s;
  animation-duration: 0.93s;
}

.ball-grid-beat > div {
  background-color: #fff;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  margin: 2px;
  animation-fill-mode: both;
  display: inline-block;
  float: left;
  animation-name: ball-grid-beat;
  animation-iteration-count: infinite;
  animation-delay: 0;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #5A0569;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
}

.loader .spinner {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 0;
  border-left: 4px solid #FFF;
  animation: spin 1s linear infinite;
}

.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(90, 2, 107, 0.95);
  z-index: 999;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.navover span {
  display: none;
  color: #fff;
  font-size: 25px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.navover.open span {
  display: block;
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #5A0569;
  z-index: 999;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform-origin: right;
  transform: rotateY(-90deg);
}

html[dir="ltr"] .sideNav {
  right: unset;
  left: 0;
}

html[dir="ltr"] .sideNav {
  transform-origin: left;
}

.sideNav.open {
  transform: rotateY(0deg);
}

.sideNav.open .options {
  opacity: 1;
}

.sideNav.open ul {
  opacity: 1;
}

.sideNav.open ul li {
  transform: translateX(0) !important;
  opacity: 1;
}

.sideNav.origin {
  transform-origin: left;
}

html[dir="ltr"] .sideNav.origin {
  transform-origin: right;
}

.sideNav .options {
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 50px;
  opacity: 0;
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fff;
  width: 32px;
  height: 32px;
  background-color: transparent;
}

.sideNav .options .close1 {
  display: flex;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}

.sideNav .options .close1 span {
  color: #fff;
}

html[dir="ltr"] .sideNav .options .close1 span {
  color: #fff;
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.sideNav > ul > li {
  position: relative;
  padding: 5px 0;
  transition: 0.5s;
  transform: translateX(10px);
  opacity: 0;
}

html[dir="ltr"] .sideNav > ul > li {
  transform: translateX(-10px);
}

.sideNav > ul > li:nth-child(1) {
  transition-delay: 0.15s;
}

.sideNav > ul > li:nth-child(2) {
  transition-delay: 0.3s;
}

.sideNav > ul > li:nth-child(3) {
  transition-delay: 0.45s;
}

.sideNav > ul > li:nth-child(4) {
  transition-delay: 0.6s;
}

.sideNav > ul > li:nth-child(5) {
  transition-delay: .75s;
}

.sideNav > ul > li:nth-child(6) {
  transition-delay: .9s;
}

.sideNav > ul > li:nth-child(7) {
  transition-delay: 1.05s;
}

.sideNav > ul > li:nth-child(8) {
  transition-delay: 1.2s;
}

.sideNav > ul > li:nth-child(9) {
  transition-delay: 1.35s;
}

.sideNav > ul > li.drop > a {
  position: relative;
}

.sideNav > ul > li.drop > a > span {
  margin-right: 10px;
  margin-left: -20px;
  font-size: 10px;
  color: #fff;
}

html[dir="ltr"] .sideNav > ul > li.drop > a > span {
  margin-right: -20px;
  margin-left: 10px;
}

.sideNav > ul > li.drop.open span {
  transform: rotate(180deg);
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-family: "tjMd";
  position: relative;
  font-size: 17px;
  color: #fff;
  text-align: center;
  transition: 0.5s;
}

.sideNav > ul > li .megaMenu {
  display: none;
  margin-top: 10px;
}

.sideNav > ul > li .megaMenu li:not(:last-child) {
  margin-bottom: 6px;
}

.sideNav > ul > li .megaMenu li a {
  font-size: 15px;
  color: #e4e4e4;
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: 0.5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

